@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0;
  padding: 15px;
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-weight: bolder;
  color: #161616;
  font-size: 3em;
  margin: 10px 0px 10px 0px;
}

h2 {
  font-weight: 600;
  color: #161616;
  font-size: 2.5em;
  margin: 5px 0px 5px 0px;
}

h3 {
  font-weight: 600;
  color: #161616;
  font-size: 1.5em;
  margin: 5px 0px 5px 0px;
}

h4 {
  font-weight: 400;
  color: #161616;
  font-size: 1.2em;
  margin: 5px 0px 5px 0px;
}

p {
  font-weight: 100;
  color: #1a0c0c;
  font-size: 1em;
}


.App {
  text-align: left;
}

:root {
  --blue: #699CFF;
  --orange: #F3BB88;
  --pink: #F388C1;
  --mint: #8AE9DD;
  --lavender: #8E9DFD;

  --blueBack: #E1EBFF;
  --orangeBack: #FFE9D6;
  --pinkBack: #FFCFE9;
  --mintBack: #E6FDFA;
  --lavenderBack: #DEE2FF;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.home-link {
  float: left;
  font-weight: 800;
  font-size: 1.25em;
  padding: 1%;
}

.nav-link {
  font-weight: 600;
}

.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: white;
}

.made-with {
  float: left;
  padding: 1% 2% 2% 2%;
  font-size: 1.15em;
}

.heart { 
  max-width: 8%;
}

.icon {
 max-width: 80%;
}

.icon:hover {
  max-width: 80%;
  transform: translateY(-2px); 
}

.icon-pad {
  float: right;
  margin-left: 2%;
  margin-top: 1%;
}

li {
  float: right;
}

li a {
  color: #161616;
  display: block;
  text-align: center;
  padding: 14px 16px;
  font-size: 20px;
  font-weight: 400;
  text-decoration: none;
}

