}

.nav-link:hover {
  color: #699CFF;
  color: var(--blue);
}

@media only screen and (min-width: 600px) {
  .dropdown-container {
    display: none;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 600px) {
  .made-with {
    padding: 1% 2% 2% 2%;
    font-size: 1.15em;
  }

  li {
    float: none;
  }

  ul {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .icons {
    display: flex;
    flex-direction: row;
  }

  .home-link {
    display: none;
  }

  .nav-link {
    display: none;
  }

  /* Menu dropdown */
  .dropdown-container {
    position: relative;
    float: right;
  }
  
  .dropdown-btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
  }

  .dropdown {
    position: absolute;
    top: 100%;
    right: 0;
    width: 100px;
    padding: 10px;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }

  .dropdown li:hover {
    background-color: #ddd;
  }
}




